.header {
    display: flex;
    justify-content: space-between; /* Расположить элементы по краям */
    align-items: center;
    background-color: #f3f3f3; /* Цвет фона как в первом примере */
    color: #000; /* Черный текст */
    padding: 10px 20px; /* Отступы */
    border-bottom: 1px solid #ddd; /* Рамка снизу */
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Тень как в примере */
}

.burgerMenuIcon {
    cursor: pointer;
}

.header h1 {
    margin: 0; /* Убираем отступы */
    font-size: 18px; /* Регулируем размер текста */
    font-weight: 500;
}
