.sidebar {
    position: fixed;
    top: 0;
    left: -250px;
    width: 250px;
    height: 100vh;
    background-color: #ffffff;
    box-shadow: 2px 0 5px rgba(0, 0, 0, 0.5);
    transition: left 0.3s ease;
    z-index: 1000;
}

.sidebar.open {
    left: 0;
}

.background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 999;
}

.nav {
    display: flex;
    flex-direction: column;
    padding: 20px;
    gap: 15px;
}

.nav a {
    text-decoration: none; /* Убираем подчеркивание */
    color: black; /* Цвет текста ссылок */
    transition: color 0.3s ease; /* Анимация для изменения цвета */
}

.nav a:hover {
    color: gray; /* Цвет при наведении */
}

.nav a.active {
    color: blue; /* Цвет для активной ссылки */
    font-weight: bold;
}

.close {
    position: absolute; /* Позволяет разместить крестик в углу */
    top: 10px; /* Отступ сверху */
    right: 10px; /* Отступ справа */
    background: none; /* Убираем фон */
    border: none; /* Убираем рамку */
    cursor: pointer; /* Указатель при наведении */
}

.close img {
    width: 20px; /* Размер изображения крестика */
    height: 20px;
    display: block;
}
